:root {
    --primary-red: #f64f59;
}

body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #444444;
    background-color: #fff7f7;
}

pre {
    white-space: pre-wrap;       /* Since CSS 2.1 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    word-wrap: break-word;       /* Internet Explorer 5.5+ */
}

button {
    border: none;
    outline: none;
    background: transparent;
}

.btn {
    text-decoration: none;
    color: white;
}

.btn:hover {
    opacity: 0.8;
}

.btn-icon {
    margin-top: -5px;
    width: 30px;
    vertical-align: middle;
    display: inline-block;
    border-radius: 50%;
}

.btn-txt {
    padding-left: 10px;
    vertical-align: middle;
}

.menu-btn {
    padding: 12px 15px;
    display: inline-block;
}

.menu-btn:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 50%;
}

.login-btn {
    padding: 5px 20px;
    border-radius: 3px;
}

.menu-bar {
    overflow: hidden;
    background-color: #312D5C;
    box-shadow: -5px -7px 15px 1px rgba(0,0,0,0.8);
    padding-left: 5%;
    padding-right: 5%;
    height: 107px;
}

.disabled {
    pointer-events: none;
}

.app-icon-container {
    /* width: 25px;
    height: 25px; */
    display: inline-block;
    font-size: 1.5rem;
    border-radius: 50%;
}

.card {
    background: white;
    border-radius: 2px;
    display: inline-block;
    margin-right: 2em;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    transition: all ease-in-out 0.6s;
}

/* .card:hover {
    transform: translateY(-10px);
} */

.page {
    width: 50%;
    margin: 0 auto;
}

.page-title {
    font-weight: 400;
    font-size: 35px;
}

.window {
    width: 80%;
    margin: 0 auto;
    background: white;
    border-radius: 6px;
}

.title-bar {
    background-color: white;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
    color: #4d494d;
    padding-top: 5px;
    padding-bottom: 5px;
}

.buttons {
    float: left;
    padding-left: 8px;
    padding-top: 3px;
    line-height: 0;
}

.mac-btn {
    font-size: 9px;
    width: 11px;
    height: 11px;
    display: inline-block;
    border-radius: 50%;
}

.close {
    background: #ff5c5c;;
    border: 1px solid #ff5c5c;
}

.minimize {
    background: #ffbd4c;
    border: 1px solid #ffbd4c;
    margin-left: 4px;
}

.zoom {
    background: #00ca56;
    border: 1px solid #00ca56;
    margin-left: 4px;
}

.content {
    background-color: #EEEEEE;
    padding: 20px 10px;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}

.col {
    display: inline-block;
}

.col-4 {
    width: 25%;
}

.col-8 {
    width: 75%;
}

.tag {
    color: var(--primary-red);
    display: inline-block;
    padding: 5px 10px;
    border: 1px solid var(--primary-red);
    border-radius: 5px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.tag:hover,
.selected {
    background-color: var(--primary-red);
    color: white;
}

.footer {
    padding: 10px;
    border: 1px solid #d4d7dd;
    background-color: #eae9e9;
    color: #45454d;
}

.social-icon {
    width: 24px;
    height: 24px;
    transition: all ease-in-out 0.6s;
}

.social-icon:hover {
    transform: translateY(-5px);
}

@media (max-width: 860px) {
    .page {
        width: 65%;
    }
    .menu-bar {
        padding-left: 5%;
        padding-right: 5%;
    }
}

@media (max-width: 770px) {
    .page {
        width: 80%;
    }
}

@media (max-width: 660px) {
    .page {
        width: 98%;
    }
    .menu-bar {
        padding-left: 0;
        padding-right: 0;
    }
}

#standard-full-width-peso::placeholder, #standard-full-width-peso, #standard-full-width-bitcoin::placeholder, #standard-full-width-bitcoin {
    text-align: center
}

.valign-center {
    display: inline-flex;
    vertical-align: middle;
    align-items: center;
}

.valign-center svg {
    margin-bottom: 20px;
    margin-left: 20px;
}

input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}


/* MAQUETA MARVEL */


.homepage {
    min-height: 90vh;
}

.backgroundHome {
    background: #52507C;
    background-size: 100%;
}

@media (max-width: 991px) {
    .background-2 {
        padding-top: 50px;
    }
}

@media (max-width: 1280px) {
    .background-2 {
        align-items: center;
    }
}

.line-2 {
    border: 1px solid rgba(151, 151, 151, 0.18);
    margin-top: 30px;
    margin-bottom: 30px;
}

.card-container {
    background: transparent !important;
    box-shadow: none !important;
    margin-bottom: 20px;
}

#formatted-numberformat-input {
    text-align: right;
}

.money-input > div > div:before, .money-input > div > div:hover .MuiInput-underline:hover:not(.Mui-disabled):before,
.money-input > div > div:after  {
    border-bottom: 0px !important;
}

.money-input .MuiInputLabel-shrink {
    transform-origin: top right;
}

.money-input .MuiFormControl-root {
    width: 100%;
}

.money-input label {
    right: 0 !important;
    text-align: right;
    color: #FFFFFF !important;
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.29px;
    line-height: 15px;
}
.money-input input {
    color: #FFFFFF;
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 0.71px;
    line-height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}

#money-input-bitcoin {
    text-indent: 4px;
}

.card-container form button {
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 0.29px;
    line-height: 15px;
    border-radius: 22px;
    box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.05);
    background-color: #4A90E2;
    padding-top:20px;
    padding-bottom:20px;
}

.security-section {
    margin-top: 25px;
}

.noHover{
    pointer-events: none;
}

.logobox {
    position: relative;
    height: auto;
    min-height: 107px;
    /* width: 50%; */
    text-align: center;
}

.logobox img {
    height: auto;
    width: 100%;
}

.money-icon.fas.fa-chevron-down {
    margin-left: 4px;
}

@media(min-width:992px){
    .logobox {
        position: absolute;
        height: auto;
        width:40%;
        max-width: 750px;
    }

    .money-icon.fas.fa-chevron-down {
        margin-left: 10px;
    }
}

label + .MuiInput-formControl {
    margin-top: 24px !important;
}